@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;1,900&display=swap);
*{outline:none;box-sizing:border-box}body{margin:0;font-family:'Roboto', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}ul{padding:0;margin:0}li{list-style:none;display:inline-block;padding:10px 20px}a{text-decoration:none}input,textarea{border:none;font-size:1.1rem;padding:5px;font-family:'Helvetica Neue', Helvetica, Arial, sans-serif;line-height:1.4rem;box-shadow:0 0 0 1000px #fff inset !important;width:100%}label input{display:none}label{color:dimgray;font-weight:500}.wrapper{max-width:1260px;margin:0 auto;padding:0 20px}.flex{display:flex;align-items:center;justify-content:space-between}.navBar{background:dimgray;padding:10px 0}.navBar a{color:white;font-size:16px;text-transform:uppercase;font-weight:800;transition:200ms}.navBar a:hover{color:#ff4800}.news__button{text-align:end;padding:10px}.post{box-shadow:0 0 5px 1px #d6d6d6;border:1px solid #ddd;margin:10px;padding:10px}@media (max-width: 730px){.post__header{flex-direction:column;align-items:flex-start}.post__header h4{margin:10px 0 0}}.post__buttons{text-align:end}.post__buttons button{margin-left:20px}.modal{position:fixed;top:0;left:0;height:100vh;width:100vw}.modal__background{width:100%;height:100%;background:#1c1c1c;opacity:0.8}.modal__close{position:absolute;top:80px;right:100px;color:white;cursor:pointer;font-size:24px}.modal__window{position:absolute;top:40%;left:50%;transform:translate(-50%, -50%);padding:35px 40px;background:white;z-index:10}.create-post{box-shadow:0 0 5px 1px #d6d6d6;border:1px solid #ddd;margin:10px;padding:10px}.create-post__button{text-align:end}.btn{box-shadow:0 5px 12px rgba(0,0,0,0.1);border:none;padding:8px 16px;background:#ff4800;color:#fff;text-transform:uppercase;transition:200ms;cursor:pointer}.btn:hover{box-shadow:0 5px 16px rgba(0,0,0,0.16)}.auth__button{text-align:end}.auth__loginError{font-size:11px;color:#f2564d}.input{position:relative;margin:20px 0 30px;border-bottom:1px solid #ddd}.input__label{position:absolute;padding-left:7px;transform:translateY(7px);transition:transform 0.4s cubic-bezier(0.25, 0.8, 0.25, 1);cursor:text;opacity:0.7}.input__label-touched{opacity:1;transform:translate3d(-23px, -23px, 0) scale(0.67)}.input__label-textarea{padding:10px 14px}.input__error{position:absolute;right:10px;font-size:24px;color:#f2564d;border:1px solid #f2564d;padding:0 12px;border-radius:50%}.input__textarea{padding:10px 15px;font-weight:600;width:100%}.text-error{box-shadow:3px 0 20px #d6d6d6;width:225px;position:absolute;padding:8px 16px;background:white;top:50%;transform:translateY(-50%);left:-240px}.text-error__triangle{position:absolute;width:0;height:0;top:50%;transform:translateY(-50%);right:-12px;border-top:10px solid transparent;border-left:12px solid white;border-bottom:10px solid transparent}.date{font-size:12px}.error{color:#f2564d}

